<script>
  import { Swiper, SwiperSlide } from 'swiper/svelte';
  import 'swiper/swiper.scss';
  import "swiper/components/effect-coverflow/effect-coverflow.min.css"
  import "swiper/components/pagination/pagination.min.css"
  import { createEventDispatcher } from 'svelte'
  const dispatch = createEventDispatcher()
  import Modal from "../modal/Modal.svelte";

  export let showModal = false;
  export let images = [];

  // import Swiper core and required modules
  import SwiperCore, {
    Keyboard,Scrollbar,Navigation,Pagination
  } from 'swiper/core';

  // install Swiper modules
  SwiperCore.use([Keyboard,Scrollbar,Navigation,Pagination]);

  function onCloseClick (e){
    e.preventDefault();

    // when it's time to trigger the event
    dispatch('close')
  }
</script>

<Modal showModal="{showModal}" on:close={onCloseClick}>
  <div class="imagePopup-wrapper" slot="main">
    <div class="imagePopup">
      <div class="container">
        <Swiper slidesPerView="{1}" centeredSlides="{true}" spaceBetween="{5}" grabCursor="{true}" pagination='{{
  "clickable": true
}}' class="mySwiper">
          {#each images as img}
            <SwiperSlide>
              <img src={img} alt="image"/>
            </SwiperSlide>
          {/each}
        </Swiper>
      </div>
    </div>
  </div>
</Modal>

<style lang="scss">
  @import "ImagePopup";
</style>